// Touchscreen detector
// http://www.javascriptkit.com/dhtmltutors/sticky-hover-issue-solutions.shtml

const detectTouch = () => {
  let isTouch = false // variable to indicate current input type (is touch versus no touch)
  let isTouchTimer
  let curRootClass = '' // variable indicating current document root class ("isTouchscreen" or "")

  function addTouchClass () {
    clearTimeout(isTouchTimer)
    isTouch = true

    // add "isTouchscreen' class if it's not already present
    if (curRootClass !== 'isTouchscreen') {
      curRootClass = 'isTouchscreen'
      document.documentElement.classList.add(curRootClass)
    }

    // maintain "istouch" state for 500ms
    // so removeTouchClass doesn't get fired immediately following a touch event
    isTouchTimer = setTimeout(() => {
      isTouch = false
    }, 500)
  }

  function removeTouchClass () {
    // remove 'isTouchscreen' class if not triggered by a touch event and class is present
    if (!isTouch && curRootClass === 'isTouchscreen') {
      isTouch = false
      curRootClass = ''
      document.documentElement.classList.remove('isTouchscreen')
    }
  }

  // this event only gets called when input type is touch
  document.addEventListener('touchstart', addTouchClass, false)

  // this event gets called when input type is everything from touch to mouse/trackpad
  document.addEventListener('mouseover', removeTouchClass, false)
}

export default detectTouch



