import 'waypoints'

export const createWaypoint = (element, offset, direction = 'down') => {
  if (!element) {
    return
  }

  new Waypoint({ // eslint-disable-line
    element,
    handler (d) {
      if (d === direction) {
        this.element.classList.add('is-loaded')
        this.destroy()
      }
    },
    offset
  })
}

// Header logo
createWaypoint(document.querySelector('.js-way-logo'), '32%')

// Primary nav, Projects filter nav, and other items
Array.from(document.querySelectorAll('.js-way-nav')).forEach((el) => {
  createWaypoint(el, '50%')
})

// Hero items
/*Array.from(document.querySelectorAll('.js-way-heroItem')).forEach((el) => {
  createWaypoint(el, '100%')
})*/

// About text - mobile
createWaypoint(document.querySelector('.js-way-aboutMb'), '80%')

// About text - desktop
createWaypoint(document.querySelector('.js-way-aboutDt'), '80%')

// Main text
