// Project items
const projItems = Array.from(document.querySelectorAll('.js-way-project-home'))
projItems.forEach((element) => {
  createWaypoint(element, '75%')
})

// Project items legacy
const legacyProjItems = Array.from(document.querySelectorAll('.js-way-projItems'))
legacyProjItems.forEach((element) => {
  createWaypoint(element, '75%')
})

// Gallery items
Array.from(document.querySelectorAll('.js-way-galleryItems')).forEach((element) => {
  createWaypoint(element, '65%')
})

// Footer CTA
createWaypoint(document.querySelector('.js-way-footerCTA'), '100%')

// Footer - "Watch Next" text
createWaypoint(document.querySelector('.js-way-watchNext'), '80%')

// Footer links
createWaypoint(document.querySelector('.js-way-footerLinks'), '97%')

// Contact
Array.from(document.querySelectorAll('.js-way-contact')).forEach((element) => {
  createWaypoint(element, '80%')
})

